@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
*{
  box-sizing: border-box;
  margin: 0;
  padding:0;
  font-family: 'Poppins',sans-serif;

}
.website-heading-box{
  margin-right: 20px;
  margin-top: 10px;
  margin-bottom: 0;
  display: flex;
}
.website-heading{
  color: orange;
  font-size: 50px;
  text-align: center;
  margin: auto;
  float: left;
}
.website-heading-box img{
  border-radius: 50%;
  width: 50px;
  float: left;
  margin-top: 20px;
}

.quote-box{
  width: 1440px;
  height: 650px;
  border: 15px solid grey;
  margin-top: -20px;
  margin-left: 30px;
  margin-right: 20px;
}
.input-box{
  width: 800px;
  border-radius: 20px;
  padding: 25px;
  background-color: #fff;
  margin: auto;
  margin-top: 20px;
}
.input-box .desc{
  font-size: 24px;
  text-align: center;
}
.input-box .input{
  position: relative;
  width: 100%;
  height: 50px;
}
.input-box .input input {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  background: #f4f6fc;
  font-size: 15px;
  color: #8798ad;
  border-radius: 20px;
  padding-right: 20px;
  padding-left: 20px;
}
.input-box button{
  background-color: #4CAF50; 
  border: none;
  border-radius: 20px;
  color: white;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  display: flex;
  font-size: 16px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer ;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
}
.input-box button:hover {
  background-color: greenyellow;
  color: blue;
}